___CSS_LOADER_EXPORT___.push([module.id, ".router[data-v-0f614e0b]{vertical-align:top}.card-img-top[data-v-0f614e0b]{height:15vw;-o-object-fit:cover;object-fit:cover;width:100%}.card-text-1[data-v-0f614e0b]{height:6vw}.price[data-v-0f614e0b]{color:#ff9f1a}.country[data-v-0f614e0b]{color:#85ad00}.badge-light[data-v-0f614e0b]{color:#ff9f1a}.faedit[data-v-0f614e0b]{margin-top:1.1px!important}.action[data-v-0f614e0b]{cursor:pointer}", "",{"version":3,"sources":["webpack://./resources/js/views/Films/Result.vue"],"names":[],"mappings":"AA0KA,yBACE,kBACF,CACA,+BAEE,WAAY,CACZ,mBAAiB,CAAjB,gBAAiB,CAFjB,UAGF,CACA,8BACE,UAEF,CACA,wBACE,aACF,CACA,0BACE,aACF,CACA,8BACE,aACF,CACA,yBACE,0BACF,CACA,yBACE,cACF","sourcesContent":["<script setup>\r\nimport { onMounted, ref, onUnmounted } from \"vue\";\r\nimport { RouterLink, RouterView, useRouter, useRoute } from \"vue-router\";\r\nimport { useFilmsObject } from \"../../stores\";\r\nimport { notyf } from \"../../components/noytf\";\r\nimport moment from \"moment\";\r\nimport Swal from \"sweetalert2\";\r\n\r\nconst filmsObject = useFilmsObject();\r\nconst route = useRoute();\r\nconst films = ref([]);\r\nconst notif = notyf;\r\n\r\nconst truncateDescription = (str, no_words) => {\r\n  let words = str.split(\" \");\r\n  return words.length >= no_words\r\n    ? str.split(\" \").splice(0, no_words).join(\" \")\r\n    : str;\r\n};\r\n\r\nconst deleteFilm = async (slug, i) => {\r\n  Swal.fire({\r\n    title: \"Are you sure you want to delete this film?\",\r\n    text: \"You won't be able to revert this!\",\r\n    icon: \"warning\",\r\n    showCancelButton: true,\r\n    confirmButtonColor: \"#3085d6\",\r\n    cancelButtonColor: \"#d33\",\r\n    confirmButtonText: \"Yes, delete it!\",\r\n  }).then(async (result) => {\r\n    if (result.isConfirmed) {\r\n      await $http.delete(\"films/\" + slug).then((response) => {\r\n        notif.success(\"Deleted Successfully!\");\r\n        films.value.splice(i, 1);\r\n      });\r\n    }\r\n  });\r\n};\r\n\r\nonMounted(async () => {\r\n  films.value = filmsObject.getFilms();\r\n});\r\nonUnmounted(async () => {\r\n  filmsObject.setFilms([]);\r\n});\r\n</script>\r\n\r\n<template>\r\n  <div class=\"text-center pt-3 pr-4 pb-3\">\r\n    <h2>Search Results</h2>\r\n  </div>\r\n  <div class=\"d-inline mb-5\" v-if=\"!films.length\">\r\n    <img\r\n      class=\"img-responsive\"\r\n      style=\"width: 100%\"\r\n      :src=\"'/images/no_records.png'\"\r\n      alt=\"Card image cap\"\r\n    />\r\n  </div>\r\n  <template v-for=\"(film, i) in films\" :key=\"i\">\r\n    <div class=\"d-inline mb-5\" v-if=\"films.length\">\r\n      <div\r\n        class=\"card d-inline-flex m-3 mb-5\"\r\n        style=\"width: 20rem; vertical-align: top\"\r\n      >\r\n        <router-link\r\n          :to=\"{ name: 'ShowFilm', params: { slug: film.slug } }\"\r\n          class=\"link\"\r\n        >\r\n          <img\r\n            class=\"card-img-top\"\r\n            :src=\"film.photoSrc ? film.photoSrc : '/images/placeholder.png'\"\r\n            alt=\"Film Image\"\r\n          />\r\n        </router-link>\r\n        <div class=\"card-body\">\r\n          <router-link\r\n            :to=\"{ name: 'ShowFilm', params: { slug: film.slug } }\"\r\n            class=\"btn text-left m-0 p-0\"\r\n          >\r\n            <h5 class=\"card-title\">{{ film.name }}</h5>\r\n          </router-link>\r\n          <div class=\"d-inline float-right\">\r\n            <router-link\r\n              :to=\"{ name: 'EditFilm', params: { slug: film.slug } }\"\r\n              class=\"btn text-left m-0 p-0\"\r\n            >\r\n              <span class=\"action text-primary mr-1 faedit\"\r\n                ><i class=\"fa fa-edit\"></i\r\n              ></span>\r\n            </router-link>\r\n            <span\r\n              @click=\"deleteFilm(film.slug, i)\"\r\n              class=\"float-right action text-danger\"\r\n              ><i class=\"fa fa-trash\"></i\r\n            ></span>\r\n          </div>\r\n          <p class=\"card-text-1\">\r\n            {{ truncateDescription(film.description, 10) }}\r\n            <span class=\"badge badge-light\">\r\n              <router-link\r\n                :to=\"{ name: 'ShowFilm', params: { slug: film.slug } }\"\r\n                class=\"m-0 p-0\"\r\n                >Read More</router-link\r\n              ></span\r\n            >\r\n          </p>\r\n          <div class=\"row\">\r\n            <div class=\"col-sm-4\">\r\n              <p class=\"card-text mb-0\">\r\n                <small class=\"text-muted\">Rating</small>\r\n                <small class=\"text-muted ml-1\">{{ film.rating }}</small>\r\n              </p>\r\n              <star-rating\r\n                :star-size=\"15\"\r\n                :show-rating=\"false\"\r\n                :name=\"'rating'\"\r\n                :active-color=\"'#ff9f1a'\"\r\n                :id=\"'staticRatings'\"\r\n                :title=\"film.rating\"\r\n                :read-only=\"true\"\r\n                v-model:rating=\"film.rating\"\r\n                :hover:rating=\"true\"\r\n                :increment=\"0.5\"\r\n              ></star-rating>\r\n            </div>\r\n            <div class=\"col-sm-4\">\r\n              <p class=\"card-text mb-0\">\r\n                <small class=\"text-muted\">Ticket Price</small>\r\n              </p>\r\n              <p class=\"card-text mb-0\">\r\n                <small class=\"price\">${{ film.ticket_price }}</small>\r\n              </p>\r\n            </div>\r\n            <div class=\"col-sm-4\">\r\n              <p class=\"card-text mb-0\">\r\n                <small class=\"text-muted\">Country</small>\r\n              </p>\r\n              <p class=\"card-text mb-0\">\r\n                <small class=\"country\">{{ film.country }}</small>\r\n              </p>\r\n            </div>\r\n          </div>\r\n          <div class=\"row\">\r\n            <div class=\"mt-3 ml-4\">Genre:</div>\r\n            <div class=\"col-sm-12 mt-2\">\r\n              <template v-if=\"Array.isArray(film.genre)\">\r\n                <span\r\n                  class=\"badge badge-light p-2 mb-2 ml-1\"\r\n                  v-for=\"(genre, index) in film.genre\"\r\n                  :key=\"index\"\r\n                  >{{ genre }}</span\r\n                >\r\n              </template>\r\n              <template v-else>\r\n                <span class=\"badge badge-light p-2 mb-2 ml-1\">{{\r\n                  film.genre\r\n                }}</span>\r\n              </template>\r\n            </div>\r\n          </div>\r\n\r\n          <!-- <a href=\"#\" class=\"btn btn-primary\">Go somewhere</a> -->\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </template>\r\n  <div class=\"mb-5\"></div>\r\n</template>\r\n<style scoped>\r\n.router {\r\n  vertical-align: top;\r\n}\r\n.card-img-top {\r\n  width: 100%;\r\n  height: 15vw;\r\n  object-fit: cover;\r\n}\r\n.card-text-1 {\r\n  height: 6vw;\r\n  /* height: auto; */\r\n}\r\n.price {\r\n  color: #ff9f1a;\r\n}\r\n.country {\r\n  color: #85ad00;\r\n}\r\n.badge-light {\r\n  color: #ff9f1a;\r\n}\r\n.faedit {\r\n  margin-top: 1.1px !important;\r\n}\r\n.action {\r\n  cursor: pointer;\r\n}\r\n</style>"],"sourceRoot":""}]);
